<template>
  <ol>
    <li v-for="item in list" :key="item.msg">
      <h3>{{ item.msg }}</h3>
    </li>
  </ol>
</template>

<script>

export default {
  setup () {
    return {
      list: [
        { msg: 'Vue 2.x 有许多全局 API 和配置，这些 API 和配置可以全局改变 Vue 的行为。如 Vue.component/directive/filter 等' },
        { msg: '虽然这种声明方式很方便，但它也会导致一些问题。从技术上讲，Vue 2 没有“app”的概念，我们定义的应用只是通过 new Vue() 创建的根 Vue 实例。从同一个 Vue 构造函数创建的每个根实例共享相同的全局配置' },
        { msg: '全局配置使得在同一页面上的多个“app”之间共享同一个 Vue 副本非常困难。为了解决这些问题，vue3.x 引入了 createApp' },
        { msg: '同时的，Vue 也由一个构造函数变成了一个对象' }
      ]
    }
  }
}
</script>
